import { NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import { getUserFromCookie } from "@/lib/auth";

export async function POST(req) {
  try {
    const user = await getUserFromCookie(req);
    if (!user) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

    const { friendId } = await req.json();
    if (!friendId) return NextResponse.json({ error: "Friend ID required" }, { status: 400 });

    // تحقق إذا الطلب موجود بالفعل
    const existing = await prisma.friendRequest.findFirst({
      where: { fromId: user.id, toId: friendId },
    });

    if (existing) return NextResponse.json({ error: "Request already sent" }, { status: 400 });

    const request = await prisma.friendRequest.create({
      data: { fromId: user.id, toId: friendId, status: "PENDING" },
    });

    return NextResponse.json({ request });
  } catch (err) {
    console.error(err);
    return NextResponse.json({ error: "Failed to send friend request" }, { status: 500 });
  }
}
